var env = process.env;

var settings = {

	//
	gbs: {
		host:  (env.NODE_ENV === 'development' ? 'http://dev.dingo.com/api' : 'http://dingo.mfd.p.day900.com/api'),
		// host: 'http://dev.dingo.com/api', //slsadmin.api.sls.com,Nginx
		db_prefix: 'sls_admin_', //key
	},

	//
	cbs: {
		/**
		 * ajax200
		 * @param  {object} err 
		 */
		statusError(err) {
			console.log(2323)
			console.log(err)
			if (err.status_code !== 404) {
				this.$message({
					showClose: true,
					message: '' + err.msg,
					type: 'error'
				});
			} else {
				this.$store.dispatch('remove_userinfo').then(() => {
					this.$alert(err.status_code + ',' + err.msg + '', '', {
						confirmButtonText: '',
						callback: action => {
							this.$router.push('/login');
						}
					});
				});
			}
		},

		/**
		 * ajax
		 */
		requestError(err) {
			this.$message({
				showClose: true,
				message: '' + err.response.status_code + ',' + err.response.statusText,
				type: 'error'
			});
		}
	}
};


module.exports = settings;


